* 作者：陈凯
* 描述：手动封装一个select选择器
*/

<template>
  <div class="choose-wrap" :class="{'disabled': disabled}" @click.stop="showWrapper" v-document-click="documentClick">
    <div class="choose">
      <span>{{value}}</span>
      <i :class="{'el-icon-caret-bottom': !showWrapperActive, 'el-icon-caret-top': showWrapperActive}"></i>
      <div class="mt-content" :class="{'active': showWrapperActive}">
        <h2>{{title}}</h2>
        <div :class="['wrapper', className]">
          <div class="col" v-for="(listData, index) in renderList" :key="index">
            <span
              class="mt-item"
              :class="{active: value === item}"
              v-for="(item, index) in listData"
              :key="index"
              @click="changeValue(item)"
            >
              {{item.name}}
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['list', 'title', 'value', 'showWrapperActive', 'disabled', 'className'],
  data () {
    return {}
  },
  computed: {
    renderList () {
      let col = Math.ceil(this.list.length / 12)
      let resultList = []
      for (let i = 0; i < col; i++) {
        let data = this.list.slice(i * 12, i * 12 + 12)
        resultList.push(data)
      }
      return resultList
    }
  },
  methods: {
    showWrapper () {
      if (!this.disabled) {
        this.$emit('change_active', !this.showWrapperActive)
      }
    },
    documentClick () {
      this.$emit('change_active', false)
    },
    changeValue (item) {
      this.$emit('changeValue', item)
      this.$emit('change_active', false)
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/changeCity/select.scss';
</style>
