* 时间：2019-01-10
* 作者：陈凯
* 描述：每一个商品的组件
*/

<template>
  <div class="s-item">
    <dl>
      <dt>
        <img :src="meta.image" alt="">
      </dt>
      <dd>
        <h3>
          <router-link :to="{name: 'productDetail'}">{{meta.title}}</router-link>
        </h3>
        <p>
          <el-rate
            v-model="meta.score"
            disabled
            show-score
            text-color="#ff9900">
          </el-rate>
          <span>很好</span> <!--4.5 很好，4好，3.5不错，3一般，很差-->
          <span>{{meta.score}}分</span>
          <span class="s-item-comment-total">{{meta.commentNum}}评论</span>
        </p>
        <p>
          <span class="s-item-tab">
            <span v-for="(v, i) in meta.tab" :key="v">{{v + (meta.tab.length - 1 > i ? '|' : '')}}</span>
          </span>
          <span class="s-item-addR">{{meta.address}}</span>
          <span>
            <a href="#">
              <i class="el-icon-location" />查看地图
            </a>
          </span>
        </p>
        <p class="s-item-price">
          人均￥{{meta.avgPrice}}
        </p>
        <ul class="deal-items">
          <li
            class="items"
            v-for="(item, index) in metaDealItems"
            :key="index"
          >
            <p class="deal-title">{{item.title}}</p>
            <p>
              <span class="deal-price">
                ￥{{item.price}}
              </span>
              <span class="deal-old-price">门市价￥{{item.counterPrice}}</span>
              <span class="deal-sales">已销售{{item.saleNum}}</span>
            </p>
          </li>
          <p class="toggle-btn" v-if="meta.dealItems.length > 2" @click="changeMore">
            <span>更多{{meta.dealItems.length}}优惠</span>
            <i :class="{'el-icon-arrow-down': !moreFlag, 'el-icon-arrow-up': moreFlag}"></i>
          </p>
        </ul>
      </dd>
    </dl>
  </div>
</template>

<script>
export default {
  props: ['meta'],
  data () {
    return {
      moreFlag: false
    }
  },
  computed: {
    metaDealItems () {
      if (!this.moreFlag) {
        return this.meta.dealItems.slice(0, 2)
      }
      return this.meta.dealItems
    }
  },
  methods: {
    changeMore () {
      this.moreFlag = !this.moreFlag
    }
  }
}
</script>
