<template>
  <div class="page-login">
    <div class="login-header">
      <a class="logo"></a>
    </div>
    <div class="login-panel">
      <div class="banner">
        <img src="//s0.meituan.net/bs/file/?f=fe-sso-fs:build/page/static/banner/www.jpg" alt="">
      </div>
      <div class="form">
        <h4 class="tips" v-if="error">{{error}}</h4>
        <p>
          <span class="loginTitle">账号登录</span>
        </p>
        <el-input
          :class="{error: error && !userName}"
          placeholder="手机号/用户名/邮箱"
          prefix-icon="profile"
          v-model="userName">
        </el-input>
        <el-input
          :class="{error: error && !userName}"
          placeholder="密码"
          prefix-icon="passworld"
          type="password"
          v-model="passWorld">
        </el-input>
        <div class="foot">
          <router-link :to="{name: 'forget'}">忘记密码？</router-link>
        </div>
        <el-button class="btn-login" type="success" @click="submit">登录</el-button>
        <p class="sing-up-guide">
          没有账号？
          <router-link :to="{name: 'register'}" target="_top">免费注册</router-link>
        </p>
        <div class="oauth-wrapper">
          <h3 class="title-wrapper">
            <span class="title">用合作网站账号登录</span>
          </h3>
          <div class="oauth cf">
            <a
              class="oauth__link oauth__link--qq"
              href=""
              target="_blank"
            ></a>
            <a
              class="oauth__link oauth__link--weibo"
              href=""
              target="_blank"
            ></a>
          </div>
        </div>
      </div>
    </div>
    <footer>
      <ul>
        <li><a href="#">关于美团</a></li>
        <li><a href="#">加入我们</a></li>
        <li><a href="#">商家入驻</a></li>
        <li><a href="#">帮助中心</a></li>
        <li><a href="#">美团手机版</a></li>
      </ul>
      <p>©2019 美团网团购 meituan.com 京ICP证070791号 京公网安备11010502025545号</p>
    </footer>
  </div>
</template>

<script>
import api from '../../api/index.js'
export default {
  data () {
    return {
      userName: '',
      passWorld: '',
      error: ''
    }
  },
  methods: {
    submit () {
      if (!this.userName) {
        this.error = '请输入账号'
        return false
      }
      if (!this.passWorld) {
        this.error = '请输入密码'
        return false
      }
      this.error = ''
      api.login({
        userName: this.userName,
        password: this.passWorld
      }).then(res => {
        if (res.data.status === 'success') {
          this.$router.push({ name: 'index' })
          this.$store.commit('setUserName', this.userName)
        } else {
          this.error = res.data.msg
        }
      })
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/login/index.scss';
</style>
