<template>
  <div class="m-like">
    <dl>
      <dt><span>猜你喜欢</span></dt>
      <dd v-for="item in likeList" :key="item.itemId">
        <p class="m-like__img">
          <img :src="item.imgUrl" alt="">
        </p>
        <p class="m-like__title">{{item.title}}</p>
        <p class="m-like__rate">
          <!--<el-rate-->
            <!--v-model="item.score"-->
            <!--disabled-->
            <!--text-color="#F90">-->
          <!--</el-rate>-->
          <span class="comment">{{item.commentNum}}个评论</span>
        </p>
        <p class="m-like__areaName">{{item.areaName}}</p>
        <p class="m-like__price">￥<span>{{item.lowPrice}}</span>起</p>
      </dd>
    </dl>
  </div>
</template>

<script>
import api from '../../api/index.js'
export default {
  data () {
    return {
      likeList: []
    }
  },
  created () {
    api.getRecommendedProducts().then(res => {
      let { data } = res.data
      this.likeList = data
    })
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/products/like.scss';
</style>
