<template>
  <div class="m-menu">
    <dl class="nav" @mouseleave="menuLeave">
      <dt>全部分类</dt>
      <dd  v-for="item in navList" :key="item.name" @mouseenter="menuEnter(item)">
        <i :class="item.icon"></i>
        {{item.name}}
        <span class="arrow"></span>
      </dd>
    </dl>
    <div v-if="curDetail" class="detail" @mouseenter="detailEnter" @mouseleave="datailLeave">
      <template v-for="(item, index) in curDetail">
        <h4 :key="index">{{item.title}}</h4>
        <span v-for="(v, i) in item.label" :key="v + '_' + i">{{v}}</span>
      </template>
    </div>
  </div>
</template>

<script>
import api from '../../api/index.js'
export default {
  data () {
    return {
      curDetail: null,
      navList: [{
        name: '美食',
        icon: 'food',
        children: [
          {
            title: '美食',
            label: ['代金券', '甜点饮品', '火锅', '自助餐', '小吃']
          }
        ]
      }, {
        name: '外卖',
        icon: 'takeout'
      }, {
        name: '酒店',
        icon: 'hotel'
      }, {
        name: '秦国民宿',
        icon: 'homestay'
      }, {
        name: 'movie',
        icon: 'food'
      }, {
        name: '机票/火车票',
        icon: 'airport'
      }, {
        name: '休闲娱乐/KTV',
        icon: 'ktv'
      }, {
        name: '生活服务',
        icon: 'life'
      }, {
        name: '丽人/美发/医学美容',
        icon: 'hair'
      }, {
        name: '结婚/婚纱摄影/婚宴',
        icon: 'marry'
      }, {
        name: '亲子/儿童乐园/幼教',
        icon: 'offspring'
      }, {
        name: '运动健身/健身中心',
        icon: 'sport'
      }, {
        name: '家装/建材/家居',
        icon: 'furniture'
      }, {
        name: '学习培训/音乐培训',
        icon: 'study'
      }, {
        name: '医疗健康/宠物/爱车',
        icon: 'health'
      }, {
        name: '酒吧/密室逃脱',
        icon: 'bar'
      }]
    }
  },
  created () {
    api.getMenuList().then(res => {
      // let { data } = res.data
      // console.log(data)
    })
  },
  methods: {
    menuEnter (item) {
      this.curDetail = item.children
    },
    menuLeave () {
      let self = this
      this.timer = setTimeout(() => {
        self.curDetail = null
      }, 200)
    },
    detailEnter () {
      clearTimeout(this.timer)
    },
    datailLeave () {
      this.curDetail = null
    }
  }
}
</script>
