<template>
  <div class="category">
    <dl class="m-category">
      <dt>按拼音首字母选择：</dt>
      <dd v-for="(item, index) in list" :key="index">
        <a :href="`#city-${item}`">{{item}}</a>
      </dd>
    </dl>
    <dl class="m-category-section" v-for="(item, index) in cityGroup" :key="index" :id="`city-${index}`">
      <dt>{{index}}</dt>
      <dd>
        <span @click="changeCity(city)" v-for="city in item" :key="city.id">{{city.name}}</span>
      </dd>
    </dl>
  </div>
</template>

<script>
import api from '../../api/index.js'
export default {
  data () {
    return {
      list: 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.slice(' '),
      cityList: [{
        'id': 1,
        'name': 'beijing',
        'acronym': 'bj',
        'rank': 'S',
        'firstChar': 'b'
      }],
      cityGroup: {}
    }
  },
  created () {
    api.getCityList().then(res => {
      let { data } = res.data
      let obj = {}
      data.forEach((item, index) => {
        if (!obj[item.firstChar.toUpperCase()]) {
          obj[item.firstChar.toUpperCase()] = []
        }
        obj[item.firstChar.toUpperCase()].push(item)
      })
      console.log(obj)
      this.cityGroup = obj
    })
  },
  methods: {
    changeCity (city) {
      this.$store.dispatch('setPosition', city)
      this.$router.push({ name: 'index' })
    }
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/changeCity/category';
</style>
