<template>
  <div class="m-products-list">
    <el-row>
      <ul>
        <li
          v-for="item in nav"
          :key="item.key"
          :class="{'s-nav-active': item.active}"
        >
          {{item.name}}
        </li>
      </ul>
    </el-row>
    <el-row>
      <item v-for="(item, index) in productList" :key="index" :meta="item"/>
    </el-row>
  </div>
</template>

<script>
import Item from './item'
import api from '../../api/index.js'
export default {
  components: {
    Item
  },
  data () {
    return {
      nav: [{
        key: 's-default',
        name: '智能排序',
        active: true
      }, {
        key: 's-price',
        name: '价格最低'
      }, {
        key: 's-score',
        name: '人气最高'
      }, {
        key: 's-comment',
        name: '评价最高'
      }],
      productList: []
    }
  },
  created () {
    api.getProductsList().then(res => {
      let { data } = res.data
      this.productList = data
    })
  }
}
</script>

