* 描述：商品详情内容结构
*/
<template>
  <div class="page-product">
    <el-row class="m-crumbs">
      <crumbs />
    </el-row>
    <el-row class="m-product__content">
      <el-col :span="19">
        <el-row>
          <category />
        </el-row>
        <el-row>
          <list />
        </el-row>
      </el-col>
      <el-col :span="5">
        <el-row class="m-map" :class="{'mapFixed': mapFixedClass}">
          <m-map />
        </el-row>
        <el-row :class="{'heightFixed': mapFixedClass}" height="0"/>
        <el-row>
          <m-like />
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Crumbs from '../../components/products/crumbs'
import Category from '../../components/products/category'
import List from '../../components/products/list'
import MMap from '../../components/products/map'
import MLike from '../../components/products/like'
export default {
  components: {
    Crumbs,
    Category,
    List,
    MMap,
    MLike
  },
  data () {
    return {
      scrollTop: '',
      maxHeight: ''
    }
  },
  computed: {
    mapFixedClass () {
      if (this.scrollTop > 256) {
        return true
      }
      return false
    }
  },
  mounted () { // 给window添加一个滚动滚动监听事件
    window.addEventListener('scroll', this.handleScroll)
  },
  methods: {
    handleScroll () { // 改变元素#searchBar的top值
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      this.scrollTop = scrollTop
      let mainHeight = document.getElementsByClassName('el-main')[0]
      this.maxHeight = mainHeight.clientHeight
    }
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="scss">
  @import '../../assets/css/products/index';
</style>
