* 作者：陈凯
* 描述：首页头部搜索框组件
*/
<template>
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <el-row :span="3" class="left">
        <img src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt="美团">
      </el-row>
      <el-row :span="15" class="center">
        <div class="wrapper">
          <el-input v-model="searchWord"
                    placeholder="搜索商家或者地址"
                    @focus="focus"
                    @blur="blur"
                    @input="inputEvent"
          >
          </el-input>
          <el-button type="primary" icon="el-icon-search">搜索</el-button>
          <dl class="hotPlace" v-if="isHotPlace">
            <dt>热门搜索</dt>
            <dd v-for="(item, index) in hotPlaceList" :key="index">
              <router-link :to="{name: 'goods', params: {name: item}}">{{item}}</router-link>
            </dd>
          </dl>
          <dl class="searchList" v-if="isSearchList">
            <dd v-for="(item, index) in searchList" :key="index">
              <router-link :to="{name: 'goods', params: {name: item}}">{{item}}</router-link>
            </dd>
          </dl>
        </div>
        <p class="suggest">
          <router-link
            :to="{name: 'goods', params: {name: item}}"
            v-for="(item, index) in suggestList"
            :key="index"
          >
            {{item}}
          </router-link>
        </p>
      </el-row>
    </el-row>
  </div>
</template>

<script>
import api from '../../api/index.js'
export default {
  data () {
    return {
      searchWord: '',
      isFocus: false,
      hotPlaceList: [],
      searchList: [],
      suggestList: []
    }
  },
  created () {
    api.getSearchHotWord().then(res => {
      let { data } = res.data
      if (data.length <= 0) {
        return
      }
      this.hotPlaceList = data.slice(0, 5)
      this.suggestList = data
    })
  },
  computed: {
    isHotPlace () {
      return this.isFocus && !this.searchWord
    },
    isSearchList () {
      return this.isFocus && this.searchWord
    }
  },
  methods: {
    focus () {
      this.isFocus = true
    },
    blur () {
      let self = this
      setTimeout(() => {
        self.isFocus = false
      }, 200)
    },
    inputEvent () {
      api.getSearchList().then(res => {
        let { data } = res.data
        if (data.length <= 0) {
          return
        }
        this.searchList = data.list
      })
    }
  }
}
</script>
